import { Component,OnChanges,Input } from '@angular/core';

@Component({
  selector: 'app-shared',
  templateUrl: './shared.component.html',
  styleUrls: ['./shared.component.css']
})
export class SharedComponent implements  OnChanges {
    @Input() rating: number;
    starWidth: number;
  constructor() { }
    ngOnChanges(): void {
        this.starWidth = this.rating * 86 /5;
    }

  ngOnInit() {
  }

}



